<template>
	<view class="content">
		<head-nav title="网易云音乐" :icon="false" color="black"></head-nav>
		<view class="container">
			<scroll-view scroll-y="true">
				
				<view class="index-search">
					<text class="iconfont icon-search"></text>
					<input type="text" placeholder="搜索歌曲">
				</view>
				<swiper-uni :banners="banners"></swiper-uni>
				<view class="index-list">
					<view class="index-list-item" v-for="(item, index) in tabList" :key="index"
						@click="toList(item.id)">
						<view class="index-list-img">
							<image :src="item.coverImgUrl" mode=""></image>
							<text>{{ item.updateFrequency }}</text>
						</view>
						<view class="index-list-text">
							<view v-for="(item, index) in item.tracks" :key="index">
								{{ index + 1 }}. {{ item.first }} - {{ item.second }}
							</view>
						</view>
					</view>
				</view>
			</scroll-view>
		</view>
	</view>
</template>

<script>
	import SwiperUni from '@/components/Swiper/Swiper.vue'
	import HeadNav from '@/components/Head/Head.vue'
	export default {
		data() {
			return {
				tabList: [],
				banners: []
			}
		},
		components: {
			HeadNav,
			SwiperUni
		},
		onLoad() {
			this.getTabList()
			uni.showLoading({
				title: '加载中...'
			})
			this.getBanner()
		},
		methods: {
			getTabList() {
				this.$request('/toplist/detail')
					.then(res => {
						this.tabList = res.list
						this.tabList.length = 4;
						uni.hideLoading()
					})
			},
			toList(id) {
				uni.navigateTo({
					url: '/pages/LIst/LIst?id=' + id
				})
			},
			getBanner() {
				this.$request('/banner').then(res => {
					this.banners = res.banners
				})
			}
		}
	}
</script>

<style scoped>
	.index-search {
		display: flex;
		align-items: center;
		margin: 70rpx 30rpx 30rpx 30rpx;
	}

	.index-search text {
		margin-left: 30rpx;
		margin-right: 30rpx;
	}

	.index-list-item {
		display: flex;
		margin: 20rpx 30rpx;
	}

	.index-list-img {
		position: relative;
		width: 212rpx;
		height: 212rpx;
		border-radius: 20rpx;
		overflow: hidden;
	}

	.index-list-img image {
		width: 212rpx;
		height: 212rpx;
	}

	.index-list-img text {
		position: absolute;
		left: 30rpx;
		bottom: 30rpx;
		font-size: 20rpx;
		color: white;
	}

	.index-list-text {
		flex: 1;
		margin-left: 22rpx;
		line-height: 66rpx;
		font-size: 24rpx;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
</style>
